<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder="姓名"></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsers">查询</el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd">新增</el-button>
				</el-form-item>
        <el-form-item>
					<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">批量删除</el-button>
				</el-form-item>
			</el-form>
		</el-col>
		<!--列表-->
		<el-table :data="users" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="selection" min-width="5%">
			</el-table-column>
			<el-table-column type="index" min-width="15%">
			</el-table-column>
			<el-table-column prop="param1" label="厂家" min-width="10%">
			</el-table-column>
			<el-table-column prop="param2" label="设备型号" min-width="10%">
			</el-table-column>
      <el-table-column prop="param3" label="注册日期" min-width="10%">
			</el-table-column>
      <el-table-column prop="param4" label="设备类型" min-width="10%">
			</el-table-column>
      <el-table-column prop="param5" label="序列号" min-width="10%">
			</el-table-column>
      <el-table-column prop="param6" label="安装位置" min-width="10%">
			</el-table-column>
      <el-table-column prop="param7" label="机构" min-width="10%">
			</el-table-column>
      <el-table-column prop="param8" label="IP" min-width="10%">
			</el-table-column>
      <el-table-column prop="param9" label="GIS" min-width="10%" sortable>
			</el-table-column>
			<el-table-column label="操作" min-width="20%">
				<template slot-scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">删除</el-button>
          <el-button size="small" type="primary" @click="handleEdit(scope.$index, scope.row)">上线</el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)">下线</el-button>
				</template>
			</el-table-column>
		</el-table>

		<!--工具条-->
		<el-col :span="24" class="pagination">
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="10" :total="total" style="float:right;" background>
			</el-pagination>
		</el-col>

		<!--编辑界面-->
		<el-dialog title="编辑" :visible.sync="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
				<el-form-item label="厂家" prop="param1">
					<el-input v-model="editForm.param1" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="设备型号" prop="param2">
					<el-input v-model="editForm.param2" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="设备类型" prop="param3">
					<el-input v-model="editForm.param3" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="注册日期" prop="param4">
					<el-input v-model="editForm.param4" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="序列号" prop="param5">
					<el-input v-model="editForm.param5" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="安装位置" prop="param6">
					<el-input v-model="editForm.param6" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="机构" prop="param7">
					<el-input v-model="editForm.param7" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="IP" prop="param8">
					<el-input v-model="editForm.param8" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="GIS" prop="param9">
					<el-input v-model="editForm.param9" auto-complete="off"></el-input>
				</el-form-item>
				<!-- <el-form-item label="性别">
					<el-radio-group v-model="editForm.sex">
						<el-radio class="radio" :label="1">男</el-radio>
						<el-radio class="radio" :label="0">女</el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="年龄">
					<el-input-number v-model="editForm.age" :min="0" :max="200"></el-input-number>
				</el-form-item>
				<el-form-item label="生日">
					<el-date-picker type="date" placeholder="选择日期" v-model="editForm.birth"></el-date-picker>
				</el-form-item>
				<el-form-item label="地址">
					<el-input type="textarea" v-model="editForm.addr"></el-input>
				</el-form-item> -->
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading">提交</el-button>
			</div>
		</el-dialog>

		<!--新增界面-->
		<el-dialog title="新增" :visible.sync="addFormVisible" :close-on-click-modal="false">
			<el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
        <el-form-item label="厂家" prop="param1">
					<el-input v-model="addForm.param1" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="设备型号" prop="param2">
					<el-input v-model="addForm.param2" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="设备类型" prop="param3">
					<el-input v-model="addForm.param3" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="注册日期" prop="param4">
					<el-input v-model="addForm.param4" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="序列号" prop="param5">
					<el-input v-model="addForm.param5" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="安装位置" prop="param6">
					<el-input v-model="addForm.param6" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="机构" prop="param7">
					<el-input v-model="addForm.param7" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="IP" prop="param8">
					<el-input v-model="addForm.param8" auto-complete="off"></el-input>
				</el-form-item>
        <el-form-item label="GIS" prop="param9">
					<el-input v-model="addForm.param9" auto-complete="off"></el-input>
				</el-form-item>
				<!-- <el-form-item label="姓名" prop="name">
					<el-input v-model="addForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="性别">
					<el-radio-group v-model="addForm.sex">
						<el-radio class="radio" :label="1">男</el-radio>
						<el-radio class="radio" :label="0">女</el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item label="年龄">
					<el-input-number v-model="addForm.age" :min="0" :max="200"></el-input-number>
				</el-form-item>
				<el-form-item label="生日">
					<el-date-picker type="date" placeholder="选择日期" v-model="addForm.birth"></el-date-picker>
				</el-form-item>
				<el-form-item label="地址">
					<el-input type="textarea" v-model="addForm.addr"></el-input>
				</el-form-item> -->
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false">取消</el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading">提交</el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	import util from '@/common/js/util'
	import { getUserListPage, removeUser, batchRemoveUser, editUser, addUser, getDeviceLists } from '@/api/api';

	export default {
    name: 'baseInfo',
		data() {
			return {
				filters: {
					name: ''
				},
				users: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//列表选中列

				editFormVisible: false,//编辑界面是否显示
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '请输入姓名', trigger: 'blur' }
					]
				},
				//编辑界面数据
				editForm: {
					param1: '厂家',
          param2: '设备型号',
          param3: '设备类型',
          param4: '注册日期',
          param5: '序列号',
          param6: '安装位置',
          param7: '机构',
          param8: 'IP',
          param9: 'GIS',
				},

				addFormVisible: false,//新增界面是否显示
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '请输入姓名', trigger: 'blur' }
					]
				},
				//新增界面数据
				addForm: {
					param1: '厂家',
          param2: '设备型号',
          param3: '设备类型',
          param4: '注册日期',
          param5: '序列号',
          param6: '安装位置',
          param7: '机构',
          param8: 'IP',
          param9: 'GIS',
				}

			}
		},
		methods: {
			//性别显示转换
			formatSex: function (row, column) {
				return row.sex == 1 ? '男' : row.sex == 0 ? '女' : '未知';
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			},
			//获取用户列表
			getUsers() {
				let para = {
					page: this.page,
					name: this.filters.name
				};
				this.listLoading = true;
				//NProgress.start();getUserListPage
				getDeviceLists(para).then((res) => {
					this.total = res.data.total;
					this.users = res.data.users;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//删除
			handleDel: function (index, row) {
				this.$confirm('确认删除该记录吗?', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					removeUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			},
			//显示编辑界面
			handleEdit: function (index, row) {
        this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//显示新增界面
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				};
			},
			//编辑
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							editUser(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '提交成功',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			//新增
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('确认提交吗？', '提示', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							para.birth = (!para.birth || para.birth == '') ? '' : util.formatDate.format(new Date(para.birth), 'yyyy-MM-dd');
							addUser(para).then((res) => {
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '提交成功',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//批量删除
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('确认删除选中记录吗？', '提示', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: ids };
					batchRemoveUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '删除成功',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			}
		},
		mounted() {
			this.getUsers();
		}
	}

</script>

<style scoped>
  .pagination {
    padding: 10px;
    margin: 10px 0px;
  }
</style>